import React, { useRef, useState } from 'react'
import Pagination from "react-js-pagination";
import RegionSelect from '../../../../components/common/Select/RegionSelect';
import SearchInput from '../../../../components/UI/Atoms/Input/SearchInput';
import PageContainer from '../../../../components/layout/PageContainer';
import { useSelect } from '../../../../hooks/basic/useSelect';
import { Grid } from '../../../../components/common/Grid/Grid';
import Description from '../../../../components/UI/Atoms/Ex/Description';
import Em from '../../../../components/UI/Atoms/Ex/Em';
import { useMutation, useQuery } from '@tanstack/react-query';
import { useDebug } from '../../../../hooks/basic/useDebug';
import assoInfoAtom from '../../../../recoil/atom/association/assoInfoAtom';
import { useRecoilValue } from 'recoil';
import { getAssociationDojangInfo, getAssociationDojangList } from '../../../../api/Association/dojangAPI';
import { phoneHyphenRegex } from '../../../../utils/lib/Regex';
import ArrowIcon from '../../../../components/UI/Atoms/Icon/ArrowIcon'
import ModalContainerSub from '../../../../components/layout/ModalContainerSub';
import { useModal } from '../../../../hooks/basic/useModal';
import { styled } from '@mui/system';
import { Box } from '@mui/material';
import { Col } from '../../../UserDojang/Manage/Student/component/StudentInfo/StudentForm/StudentForm';


const ListBox = styled(Box)(({ theme, slide }) => ({
  height: '365px',
  margin: 'auto',
  backgroundColor: '#ffffff',
  border: '1px solid #e7e7e7',
  padding: '20px 20px',
  boxShadow: '4px 4px 10px rgba(0, 0, 0, 0.05)',
  borderRadius: '10px',
  marginLeft: '5px',
  marginRight: '5px',
  transform: `translateX(${slide}px)`,
  transition: '0.5s all ease',
  '*': {
    boxSizing: 'border-box',
    cursor: 'pointer',
  },
}));

const labelStyle = {
  width: '88px',
  height: '40px',
  fontSize: '14px',
  color: '#00196A',
  padding: '10px 0 10px ',
};

const pStyle = {
  width: '267px',
  // height: '40px',
  padding: '0px 10px',
  fontSize: '14px',
  lineHeight: '20px',
  color: '#000000',
  border: 'none',
  borderBottom: '1px solid #e7e7e7',
  backgroundColor: '#ffffff',
};

const ImgStyle = styled(Box)(({ theme, props }) => ({
  width: '178px',
  height: '122px',
  backgroundImage: `url(https://tkdw-img.s3.ap-northeast-2.amazonaws.com/${props})`,
  backgroundSize: 'contain',
  backgroundRepeat: 'no-repeat',
  backgroundPosition: 'center',
  border: '1px solid #dddddd',
}));


const DojangList = () => {
  const [page, setPage] = useState(1)

  // recoil
  const assoInfo = useRecoilValue(assoInfoAtom)
  const asso = assoInfo.name === '한국' ? -1 : assoInfo.name

  // custom hook
  const [ region, handleRegion ] = useSelect(asso)
  const [ open, openFn, closeFn ] = useModal()
  
  // const [keyword, setKeyword] = useState('')

  // query
  const { data } = useQuery(['getAssociationLevelupList', region, page], () => getAssociationDojangList(-1, region, page), {
    enabled: !!region && !!page,
  })
  const { mutate } = useMutation((dojangId) => getAssociationDojangInfo(dojangId))
  // const { mutate: SearchMt } = useMutation((keyword) => )

  // handler
  const handleClick = e => {
    let dojangId = e.currentTarget.id
    mutate(dojangId, {
      onSuccess: (res) => {
        setDojangInfo(res)
        openFn()
      }
    })
  }

  // useDebug(page)
  const [dojangInfo, setDojangInfo] = useState('')

  // const handleKeyword = e => {
  //   // keyword.current = e.target.value
  //   console.log(e.target.value)
  //   setKeyword(e.target.value)
  // }

  // const handleSearchClick = e => {
  //   // console.log(year, month, region, keyword, page)
  // }

  return (
    <>
      <PageContainer>
        <div className='flex justify-between items-center mb-3 w-full'>
          <div className="flex gap-[5px]">
          { assoInfo?.name === '한국' && <RegionSelect list={assoInfo?.region_info} value={region} onChange={handleRegion} />}
          </div>
          {/* <SearchInput 
            width='250px'
            placeholder='도장 이름 검색'
            value={keyword}
            onChange={handleKeyword}
            onClick={handleSearchClick}
          /> */}
        </div>
        <Grid>
          <Grid.Head>
            <Description>현재 {region === -1 ? '전체' : region} 지역의 태권도장은 <Em>{data?.count}개</Em> 입니다.</Description>
          </Grid.Head>
          <Grid.RowWrap>
            <Grid.Row className='bg-[#fafafa] '>
              <Grid.Col fixed='225px'>도장 이름</Grid.Col>
              <Grid.Col fixed='225px'>도장 전화번호</Grid.Col>
              <Grid.Col fixed='150px'>소속 사범 수</Grid.Col>
              <Grid.Col fixed='150px'>소속 수련생 수</Grid.Col>
            </Grid.Row>
            { React.Children.toArray(
              data?.dojang_info?.map(item => 
                <Grid.Row id={item?.id} onClick={handleClick}>
                  <Grid.Col fixed='225px'>{item?.name}</Grid.Col>
                  <Grid.Col fixed='225px'>{phoneHyphenRegex(item?.phone_number)}</Grid.Col>
                  <Grid.Col fixed='150px'>{item?.sabum_count}명</Grid.Col>
                  <Grid.Col fixed='225px'>{item?.student_count}명</Grid.Col>
                  <Grid.Col><ArrowIcon /></Grid.Col>
                </Grid.Row>
              )
            )
            }
          </Grid.RowWrap>
        </Grid>

        <ModalContainerSub
          open={open}
          onClose={closeFn}
        >
          <div className="flex justify-center mt-[20px] mb-[30px]">
            <Col>
              <ImgStyle props={dojangInfo?.logo_img ? JSON.parse(dojangInfo?.logo_img)[0] : ''} />
            </Col>
          </div>
          <ListBox>
            <div>
              <div>
                <Col className='mb-[5px]'>
                  <label style={labelStyle}>도장 이름</label>
                  <p style={pStyle} name="name">
                    {dojangInfo?.name}
                  </p>
                </Col>
              </div>
              <div>
                <Col className='mb-[5px]'>
                  <label style={labelStyle}>도장 전화번호</label>
                  <p style={pStyle} name="phone_number">
                    {phoneHyphenRegex(dojangInfo?.phone_number)}
                  </p>
                </Col>
              </div>
              <div>
                <Col className='mb-[5px]'>
                  <label style={labelStyle}>도장 주소</label>
                  <p style={pStyle} name="address_name">
                    {dojangInfo?.address_name}
                  </p>
                </Col>
              </div>
              <div>
                <Col className='mb-[5px]'>
                  <label style={labelStyle}></label>
                  <p style={pStyle} name="address_detail">
                    {dojangInfo?.address_detail}
                  </p>
                </Col>
              </div>
              <div>
                <Col className='mb-[5px]'>
                  <label style={labelStyle}>소속 사범님 수</label>
                  <p style={pStyle} name="sabum_count">
                    {dojangInfo?.sabum_count}
                  </p>
                </Col>
              </div>
              <div>
                <Col className='mb-[5px]'>
                  <label style={labelStyle}>소속 수련생 수</label>
                  <p style={pStyle} name="student_count">
                    {dojangInfo?.student_count}
                  </p>
                </Col>
              </div>
            </div>
          </ListBox>
        </ModalContainerSub>

        <Pagination 
          activePage={page}
          itemsCountPerPage={30}
          totalItemsCount={data?.count}
          pageRangeDisplayed={5}
          prevPageText={'‹'}
          nextPageText={'›'}
          onChange={setPage}
        />
      </PageContainer>
    </>
  )
}

export default DojangList