import authApi from "../authApi"

export const getAssociationDojangList = async (depth_1, depth_2, page) => {
  const res = await authApi.get(`associ/as_dojang_info_read/${depth_1}/${depth_2}?page=${page}`)
  return res.data
}

export const getAssociationDojangInfo = async (dojangId) => {
  const res = await authApi.get(`associ/as_dojangread/${dojangId}`)
  return res.data
}



